.project-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 2rem;
  }

.project-card {
    background-color: #1a1919;
    padding: 1.2rem 1rem;
}

.project-card img {
    width: 100%;
}

.project-title {
    padding: 0.5rem 0;
}

.project-details p {
    display: flex;
    align-items: center;
    padding: 0.5rem 0;
}

.project-btn {
    display: flex;
    color: gray;
    justify-content: space-between;
    padding: 0.5rem 0;
    text-decoration: none;
    font-weight: bold;
    margin-right: 10px; 
}

.project-btn .btn {
    background-color: #f2f2f2;
    color: #1a1919;
    padding: 0.5rem 1rem;
}
  
@media screen and (max-width: 740px) {
    .project-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
    
}
